<template>
    <div :style="[{'position':'relative','overflow-y':'auto','height':listHeight+'px'}]" @scroll="scrollItems">

        <div :style="[{'height':itemsHeight+'px'}]"></div>

        <div :style="[{'position':'absolute','top':'0px'}]">

            <div :style="[{'height':preItemsHeight+'px'}]"></div>

            <template v-for="(item,kitem) in useItems">
                <div ref="block" :index="item.index" :key="kitem">

                    <slot name="itemBlock" :row="item.row" :irow="item.index"></slot>

                </div>
            </template>

            <div :style="[{'height':aftItemsHeight+'px'}]"></div>

        </div>

    </div>
</template>

<script>
import each from 'lodash/each'
import map from 'lodash/map'
import get from 'lodash/get'
import size from 'lodash/size'
import find from 'lodash/find'
import findLast from 'lodash/findLast'
import debounce from 'lodash/debounce'
import cint from 'wsemi/src/cint.mjs'

/**
 * @vue-prop {Array} [rows=[]] []slotslotrowirowrowsslotmargin
 * @vue-prop {Number} [listHeight=400] px400
 * @vue-prop {Number} [itemMinHeight=24] px24
 * @vue-prop {Number} [itemsPreload=40] 40
 */
export default {
    props: {
        rows: {
            type: Array,
            default: () => [],
        },
        listHeight: {
            type: Number,
            default: 400,
        },
        itemMinHeight: {
            type: Number,
            default: 24,
        },
        itemsPreload: {
            type: Number,
            default: 40,
        },
    },
    data: function() {
        return {
            preItemsHeight: 0, //
            aftItemsHeight: 0, //
            scrollTop: 0, //
            itemsHeight: 0, //
            items: [], //
            useItems: [], //
        }
    },
    watch: {

        rows: {
            immediate: true,
            deep: true,
            handler(value) {
                //console.log('watch rows', value)

                let vo = this

                //items
                let items = map(vo.rows, (v, k) => {
                    return {
                        index: k,
                        height: vo.itemMinHeight,
                        y: k * vo.itemMinHeight,
                        row: v,
                    }
                })

                //save
                vo.items = items

                //change
                vo.change()

            }
        }

    },
    computed: {
    },
    methods: {

        change: function() {
            //console.log('methods change')

            let vo = this

            vo.getUseItems()
            vo.updateItems()
            vo.getUseItems()

        },

        getUseItems: function() {
            //console.log('methods getUseItems')

            let vo = this

            //items, vo.item, cloneDeep
            let items = (vo.items)

            //y1, y2
            let y1 = vo.scrollTop
            let y2 = y1 + vo.listHeight

            //indStart, indEnd
            let n = size(items)
            let o
            o = findLast(items, (v, k) => {
                return (v.y + v.height) < y1
            })
            let indStart = get(o, 'index', 0)
            indStart = Math.max(indStart - vo.itemsPreload, 0)
            o = find(items, (v, k) => {
                return v.y > y2
            })
            let indEnd = get(o, 'index', n)
            indEnd = Math.min(indEnd + vo.itemsPreload, n - 1)

            //useItems
            let useItems = []
            let preItemsHeight = 0
            let aftItemsHeight = 0
            each(items, (v, k) => {
                if (k < indStart) {
                    preItemsHeight += v.height
                }
                else if (k > indEnd) {
                    aftItemsHeight += v.height
                }
                else {
                    useItems.push(v)
                }
            })

            //save
            vo.useItems = useItems
            vo.preItemsHeight = preItemsHeight
            vo.aftItemsHeight = aftItemsHeight

        },

        updateItems: function() {
            //console.log('methods updateItems')

            let vo = this

            //items, vo.item, cloneDeep
            let items = (vo.items)

            //update height
            each(vo.$refs.block, (v) => {
                let index = cint(v.getAttribute('index'))
                if (index >= 0 && index <= size(items) - 1) {
                    let h = v.clientHeight //margin
                    //console.log(`v.clientHeight=${v.clientHeight}, v.offsetHeight=${v.offsetHeight}, v.scrollHeight=${v.scrollHeight}, v.getBoundingClientRect()=`, v.getBoundingClientRect())
                    if (items[index].height !== h) {
                        items[index].height = h
                    }
                }
            })

            //update y
            let y = 0
            each(items, (v) => {
                if (v.y !== y) {
                    v.y = y
                }
                y += v.height
            })

            //update itemsHeight
            if (vo.itemsHeight !== y) {
                vo.itemsHeight = y
            }

        },

        scrollItems: debounce(function(e) {
            //console.log('methods scrollItems', e.target.scrollTop)

            let vo = this

            //setTimeout
            setTimeout(() => {

                //save scrollTop
                let scrollTop = e.target.scrollTop
                vo.scrollTop = scrollTop

                //change
                vo.change()

            }, 10)

        }, 100),

    },
}
</script>

<style scoped>
</style>

